.welcome-button {
    text-align:center;
    margin-top:45vh;
}

.welcome-button button {
    padding:10px 15px;
    color:white;
    border: 1px solid white;
    background-color:black;
    outline:none;
    transition:1.5s;
}

.welcome-button button:hover {
    color:black;
    border:1px solid black;
    background-color:white;
    transition:1.5s;
}

.navbar {
    display:flex;
    justify-content: space-evenly;
}

.logo {
    flex-grow:2;
    padding: 0 25px;
}

.logo h2 {
    width:28px;
    padding:5px;
    border:1px solid white;
    animation: logo 10s infinite linear;
}

@keyframes logo {
    0%{
        color:white;
        background-color:black;
        border:1px solid white;
    }
    50%{
        color:black;
        background-color:white;
        border:1px solid black;
    }
    100%{
        color:white;
        background-color:black;
        border:1px solid white;
    }
}

.navbar button{
    background-color:black;
    color:white;
    flex-grow:1;
    border:1px solid black;
    outline:none;
    transition:1.5s;
}

.navbar button:hover {
    background-color:white;
    border:1px solid white;
    color:black;
    transition:1.5s;
}

.jumbotron {
    height:calc(100vh - 128.813px);
    background-color:white;
    color:black;
    padding:25px;
    text-align: center;
}

.next-btn {
    border-radius:50%;
    padding:20px;
    outline:none;
    border:none;
}
.friends {
    display:flex;
    justify-content: space-evenly;
    flex-wrap:wrap;
}

.card {
    box-shadow: 2px 2px 5px grey;
    padding:25px;
    border:1px solid white;
    width:300px;
    margin:25px;
}
.card-content img{
    border-radius:50%;
    margin:20px;
    box-shadow:0 0 20px grey;
}
.card-content button {
    color:white;
    background-color:black;
    outline:none;
    cursor: pointer;
    border:1px solid white;
    padding:10px 15px;
    border-radius:5px;
    transition:1.5s;
}
.card-content button:hover {
    color:black;
    background-color:white;
    border:1px solid black;
    transition:1.5s;
}